import { Component } from '@angular/core';
import {Project} from './project';
import { HttpClient } from '@angular/common/http';




@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})


export class AppComponent {

  public gg:string;
  public jj:string;
  public ff:string;
  public project : Project;
  public projects : Project[] = [];
  private activeProjectId : number = 1;
  public temperature:number;
  public date:string;
  private errormessage:string;
  showHome : boolean = true;
  showProjects : boolean = false;
  showAbout : boolean = false;
  

  constructor(private http:HttpClient) {
    console.log('Constructor initialised');
    
    
   }; 

   ngOnInit()
   {
     let project = new Project();
     
     
     this.project = {id:1,address:"6148 Buck Trail rd Indianapolis IN",netOperatingIncome:"5400",cashFlow:"450",image:"../assets/images/i1.jpg"}
     this.projects.push(this.project);

     this.project = {id:2,address:"16620 Milhousen Trail Rd Westfield IN",netOperatingIncome:"7800",cashFlow:"1550",image:"../assets/images/i2.jpg"}
     this.projects.push(this.project);


     this.project = {id:3,address:"12839 Cypress Swamp dr Orlando FL",netOperatingIncome:"15000",cashFlow:"2800",image:"../assets/images/i3.jpg"}
     this.projects.push(this.project);



     this.http.get('http://api.openweathermap.org/data/2.5/weather?zip=34787,us&appid=48c9253d856985a9b820967927888247').subscribe(data => {
     this.temperature = data['main']['temp'];

      this.temperature = Math.round((this.temperature - 273.15) * 1.8 + 32);
    });



      this.http.get('http://worldtimeapi.org/api/timezone/America/New_York').subscribe(data => {
      console.log(data);
      //this.date = data['datetime'];
     
    });

    this.date = new Date().toDateString();
    

   
   }

   //kghhyjgfhjfhjfj


   OnHomeClicked()
   {
    this.showHome = true;
    this.showProjects = false;
    this.showAbout = false;

   }

   OnProjectsClicked()
   {
    this.showHome = false;
    this.showProjects = true;
    this.showAbout = false;

   }

   OnAboutClicked()
   {
    this.showHome = false;
    this.showProjects = false;
    this.showAbout = true;

   }

   OnNextImageClick()
   {
     if(this.activeProjectId < 3){this.activeProjectId++;}
     //if(this.activeProjectId >= 3){this.activeProjectId = 1;}
     
     var activeId = this.activeProjectId;
     this.project = this.projects.find(function(element) {return element.id == activeId});;
     
   }

   OnPrevImageClick()
   {
    if(this.activeProjectId > 1){this.activeProjectId--;}
   

    var activeId = this.activeProjectId;
    this.project = this.projects.find(function(element) {return element.id == activeId});
     
   }

}
